const initialItems = [
    {
        id: 0,
        name: 'Playstation 4',
        price: '5000000',
        image: 'data:image/jpeg;base64,'
    },
    {
        id: 1,
        name: 'Nintendo Switch',
        price: '6500000',
        image: 'https://static-ca.ebgames.ca/images/products/729000/3max.jpg'
    },
]

const reducer = (state, action) => {
    switch (action.type) {
        case 'add':
            return [...state, action.data]
        case 'remove':
            return []
        default:
            return initialItems
    }
}

export { initialItems, reducer }